import { useEffect, useState } from "react";
import VehicleComponent from "../vehicle/vehicle.component";
import './vehicles.components.css';
import vehicleService from "../../services/vehicle.service";

// Define a TypeScript interface for vehicle data
interface Vehicle {
    name: string;
    valor: number;
    qtdParcelas: number;
}

export default function VehiclesComponent() {
    const [vehicles, setVehicles] = useState<Vehicle[]>([]);

    useEffect(() => {
        async function fetchVehicles() {
            try {
                const response = await vehicleService.listVehicles();
                if (Array.isArray(response.data.motos)) {
                    setVehicles(response.data.motos);
                } else {
                    console.error('Unexpected data format:', response.data);
                }
            } catch (error) {
                console.error('Failed to fetch vehicles', error);
            }
        }

        fetchVehicles();
    }, []);

    useEffect(() => {
        console.log('Motos: ', vehicles);
    }, [vehicles]);

    return (
        <div className="container">
            <section className="py-5 text-center container">
                <div className="row py-lg-5">
                    <div className="col-lg-6 col-md-8 mx-auto">
                        <h1 className="fw-light">Album example</h1>
                        <p className="lead text-body-secondary">Something short and leading about the collection below—its contents, the creator, etc. Make it short and sweet, but not too short so folks don’t simply skip over it entirely.</p>
                    </div>
                </div>
            </section>


            <div className="row">
                {vehicles.map((vehicle, index) => (
                    <div className="col-4" key={index}>
                        <VehicleComponent vehicle={{
                            vehicle: vehicle
                        }} />
                    </div>
                ))}
            </div>

            <button type="button" className="btn btn-danger">Danger</button>
        </div>
    );
}
