// vehicle.component.jsx
import React, { useState } from 'react';
import './vehicle.component.css';
import PopUpComponent from '../popup/popup.component';
import { VehicleModel } from '../../models/vehicle.mode';

export default function VehicleComponent({ vehicle }: { vehicle: VehicleModel }) {
    const [openPopUp, setOpenPopUp] = useState(false);

    const handlePopUp = () => {
        setOpenPopUp(!openPopUp);
    };

    const handleClose = () => {
        setOpenPopUp(false);
    };

    return (
        <div className="card">
            <img src={`images/vehicles/${vehicle.vehicle.image}`} className="card-img-top" alt="Vehicle" />
            <div className="card-body">
                <h5 className="card-title">{vehicle.vehicle.name}</h5>
                <p className="card-text">Valor: R${vehicle.vehicle.valor.toFixed(2)}</p>
                <p className="specifications">Parcelas: {vehicle.vehicle.qtdParcelas}</p>
                <button type="button" className="btn btn-primary" onClick={handlePopUp}>Veja mais</button>
            </div>
            {openPopUp && (
                <PopUpComponent handleClose={handleClose} selectedVehicle={vehicle.vehicle} />
            )}
        </div>
    );
}
