import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './header.component.css';
import { Nav, Offcanvas } from 'react-bootstrap';
import { useState } from 'react';

export default function HeaderComponent() {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const toggleMenu = () => {
        const navBarContact = document.getElementById("navBarContact");
        const navBarMenu = document.getElementById("navBarMenu");
    }

    return (
        <nav className="navbar navbar-expand-lg">
            <div className="container-fluid m-3">
                <div className="d-flex justify-content-start">
                    <div className="m-0 p-3">
                        <button 
                            className="navbar-toggler m-1 border border-0" 
                            onClick={handleShow} 
                            type="button" 
                            aria-controls="navBarMenu" 
                            aria-expanded="false" 
                            aria-label="Toggle Menu"
                        >
                            <img src="images/dots-menu.png" className="m-2" alt="More" width={20} height={20} />
                        </button>
                    </div>
                    <div className="m-0 p-3">
                        <a className="navbar-brand mb-4" href="#">
                            <img src="images/logo.png" alt="Logo" />
                        </a>
                    </div>
                </div>
                <div className="d-flex justify-content-end">
                    <button 
                        className="navbar-toggler m-1 border border-0" 
                        onClick={toggleMenu} 
                        type="button" 
                        aria-controls="navBarContact" 
                        aria-expanded="false" 
                        aria-label="Toggle Contact"
                    >
                        <img src="images/more.png" alt="More" width={20} height={20} />
                    </button>
                </div>

                <Offcanvas 
                    show={show} 
                    onHide={handleClose} 
                    placement="end"
                    className="d-lg-none"
                >
                    <Offcanvas.Header closeButton>
                        <Offcanvas.Title>
                            <div className="container p-3">
                                <img src="images/logo.png" alt="Logo" />
                            </div>
                        </Offcanvas.Title>
                    </Offcanvas.Header>
                    <Offcanvas.Body>
                        <Nav className="flex-column">
                            <Nav.Link href="#" className="custom-nav-link bg-danger text-light">
                                Item 1
                            </Nav.Link>
                            <Nav.Link href="#" className="custom-nav-link text-black">
                                Item 2
                            </Nav.Link>
                            <Nav.Link href="#" className="custom-nav-link text-black">
                                Item 3
                            </Nav.Link>
                            <Nav.Link href="#" className="custom-nav-link text-black">
                                Item 4
                            </Nav.Link>
                        </Nav>
                    </Offcanvas.Body>
                </Offcanvas>

                <div className="collapse navbar-collapse" id="navBarContact">
                    <div className="container">
                        <ul>
                            <li><a className="link-default" href="https://wa.me/55034996360131">34-99636-0131</a></li>
                            <li><a className="link-default" href="https://wa.me/55034996360131">34-99636-0131</a></li>
                        </ul>
                    </div>
                    <article className="unit align-items-center">
                        <div className="unit-body">
                            <a className="link-default" href="tel:#">Rua Rui Barbosa, 272 <br /> Centro, Monte Alegre de Minas - MG</a>
                        </div>
                    </article>
                    <a className="button button-gray-bordered button-winona" href="#">Contato</a>
                </div>
            </div>
        </nav>
    );
}
