// popup.component.jsx

import React from 'react';
import './popup.component.css';
import { VehiclePopUpProp } from '../../models/vehicle.mode';

export default function PopUpComponent({handleClose, selectedVehicle}: {handleClose: any,  selectedVehicle: VehiclePopUpProp}) {
    return (
        <div className="modal">
            <div className="modal-content">
                <button className="btn-close" onClick={handleClose}>X</button>
                <div className="container">
                    <h1>{selectedVehicle.name}</h1>
                </div>
            </div>
        </div>
    );
}
