import './carrossel.component.css'

export default function CarrosselComponent() {
    return (
        <div className="swiper-wrapper">
            <div className="swiper-slide" style={{
                backgroundImage: "url('images/slider-minimal-slide-1-1920x968.jpg')",
                backgroundPosition: 'center',
                backgroundRepeat: 'no-repeat',
                backgroundAttachment: 'fixed',
            }}>
                <div className='container'>
                    <div className="d-none d-lg-block">

                        <ul className="navbar">
                            <li className="nav-item">
                                <a className="nav-link text-white" aria-current="page" href="#">Home</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link text-white" href="#">Features</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link text-white" href="#">Pricing</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link text-white" aria-disabled="true">Disabled</a>
                            </li>
                        </ul>

                    </div>
                    <div className="jumbotron-classNameic-content">
                        <div className="wow-outer">
                            <hr className="bar" />
                            <h2 className="sub-title">DIVERSOS MODELOS</h2>
                        </div>
                        <h1 className="title">Várias <br /> Possibilidades</h1>
                        <p className="text-white wow-outer"><span className="wow slideInDown" data-wow-delay=".35s">Descubra a liberdade sobre duas rodas com as motos Honda - potência, inovação e paixão em cada quilômetro.</span></p>
                        <div className="wow-outer button-outer"><a className="button button-md button-primary button-winona wow slideInDown" href="#" data-wow-delay=".4s">Veja Mais</a></div>
                        <button type="button" className="btn btn-danger">Danger</button>
                    </div>
                </div>
            </div>
        </div>
    )
}